    /**
     * get points for a circle (=shape of each bone)
     * @param radius
     * @param numSegments
     */
    function getCirclePoints(radius, numSegments) {
        let points = []
        for (let i = 0; i < numSegments; i++) {
            let angle = (i / numSegments) * Math.PI * 2
            let x = radius * Math.cos(angle)
            let y = radius * Math.sin(angle)
            points.push(new BABYLON.Vector3(x, y, 0))
        }
        return points
    }
    function getRotationAxis(but) {
        let rotationAxis
            switch (but) {
                case "x":
                    rotationAxis = BABYLON.Axis.X
                    break;
                case "y":
                    rotationAxis = BABYLON.Axis.Y
                    break;
                case "z":
                    rotationAxis = BABYLON.Axis.Z
                    break;
            }   
            return rotationAxis
    } 
