    /**
     * render skeleton
     * @param mesh
     * @param skeleton
     */        
    function addSkeleton(mesh,skeleton) {
        let spheres = []
    //    skeleton.prepare()
      
        skeleton.bones.forEach(function (bone) {
            if (
                !bone.name.includes("neutral") &&
                !bone.name.includes("Skelett") 

            ) {
                let info=skeletonInfo[bone.name]
                let color="#FF0000"
                if  (!bone.name.includes("Wrist") && !bone.name.includes("Shoulder")  && !bone.name.includes("Finger")   && !bone.name.includes("Thumb")) {
                    const sphere = BABYLON.MeshBuilder.CreateSphere( "joint_" + bone.name, { segments: 4, diameter: .04 },   scene)
                    sphere.attachToBone(bone, mesh)
                    sphere.bone = bone
                    sphere.renderingGroupId = 2
                
                    if (info && info.jointColor) color=info.jointColor
                    sphere.material = new BABYLON.StandardMaterial("joint")
                    sphere.material.diffuseColor = BABYLON.Color3.FromHexString(color)
                    sphere.renderForAI=true
                    sphere.aiColor=BABYLON.Color3.FromHexString(color)                      
                    spheres.push(sphere)                    
                }
                let path = []
                let child
                if (bone.children && bone.children[0]) {
                    child = bone.children[0];
                    path = [
                        new BABYLON.Vector3(0, 0, 0),
                        child.position,
                    ];
                } else {
                    //  console.log("no children", bone.name, bone)
                }
                color="#ffffff"
                if (info && info.boneColor) color=info.boneColor

                let circle = getCirclePoints(.015, 5)
                let extrusion
                if (path.length  && !bone.name.includes("Finger")   && !bone.name.includes("Thumb") && !bone.name.includes("Hand")) {
                    extrusion = new BABYLON.MeshBuilder.ExtrudeShape(
                        "bone_" + bone.name,
                        {
                            shape: circle,
                            closeShape: true,
                            path: path,
                            sideOrientation: BABYLON.Mesh.DOUBLESIDE,
                            updatable: true,
                        },
                        scene
                    )

                    extrusion.renderingGroupId = 2
                    extrusion.bone = bone
                    extrusion.child = child
                    extrusion.attachToBone(bone, mesh)
                    extrusion.material = new BABYLON.StandardMaterial("bone")
                    extrusion.renderForAI=true
                    extrusion.aiColor=BABYLON.Color3.FromHexString(color)  
                    extrusion.material.diffuseColor = BABYLON.Color3.FromHexString(color)  

                    
                }
            }
        })
        
    }
    let positionData={
        joints:{},
        root:{}
    }
    function saveJointPositions() {
        const position = BABYLON.Vector3.Zero()
        const scaling = BABYLON.Vector3.Zero()
        const rotationQ = BABYLON.Quaternion.Zero()     
        let lmat=root.getLocalMatrix()
        lmat.decompose(scaling, rotationQ, position)                
        positionData.root={scaling:scaling.clone(),rotationQ:rotationQ.clone(),position:position.clone()}
  
        for(let mesh of scene.meshes) {
            if (mesh.name.includes("joint_")) {
                let bone=mesh.bone
                let lmat=bone.getLocalMatrix()
                lmat.decompose(scaling, rotationQ, position)                

                positionData.joints[mesh.name]={scaling:scaling.clone(),rotationQ:rotationQ.clone(),position:position.clone()}
            }
        }   
    }
    function restoreJointPositions() {
        let node=root.getTransformNode()
        let info=positionData.root
        node.rotationQuaternion=new BABYLON.Quaternion(info.rotationQ._x,info.rotationQ._y,info.rotationQ._z,info.rotationQ._w)
        node.position=new BABYLON.Vector3(info.position._x,info.position._y,info.position._z)                
        node.scaling=new BABYLON.Vector3(info.scaling._x,info.scaling._y,info.scaling._z)  
        for(let mesh of scene.meshes) {
            if (mesh.name.includes("joint_")) {
                let bone=mesh.bone
                node=bone.getTransformNode()

                info=positionData.joints[mesh.name]
                node.rotationQuaternion=new BABYLON.Quaternion(info.rotationQ._x,info.rotationQ._y,info.rotationQ._z,info.rotationQ._w)
                node.position=new BABYLON.Vector3(info.position._x,info.position._y,info.position._z)                
                node.scaling=new BABYLON.Vector3(info.scaling._x,info.scaling._y,info.scaling._z)  

                if (node.rotationQuaternion) {
                    bone.rotationQuaternion = node.rotationQuaternion;
                } else {
                    bone.rotation = node.rotation;
                }
                bone.scaling = node.scaling;
            }
        }   
    }    
    /**
     * load current pose
     * return: position object
     */
    export function getPose() {
        saveJointPositions()
        let res=[JSON.parse(JSON.stringify(positionData))]
        return res
    }
    /**
     * set current pose
     * @param positionArray
     */
     export function setPose(positionArray) {
        positionData=positionArray[0]
        restoreJointPositions()
    }
    /**
     * get full scene data: camera and all poses
     */
    export async function getScene() {
        let image=await renderNormal()
        let cameraInfo={}
        let camera=scene.activeCamera
        cameraInfo.position=JSON.parse(JSON.stringify(camera.position))
        cameraInfo.rotation=JSON.parse(JSON.stringify(camera.rotation))
        let poseInfo= getPose()
        return {cameraInfo,poseInfo,image}
    }
    /**
     * get full scene data: camera and all poses without image
     */
     export function getSceneNoImage() {
        let cameraInfo={}
        let camera=scene.activeCamera
        cameraInfo.position=JSON.parse(JSON.stringify(camera.position))
        cameraInfo.rotation=JSON.parse(JSON.stringify(camera.rotation))
        let poseInfo= getPose()
        return {cameraInfo,poseInfo}
    }

    /**
     * set scene and pose data
     */
    export function setScene(sceneInfo) {
        if (!root) {    // model might not be loaded yet - does not happen very often
            setTimeout(() => {
                setPose(sceneInfo.poseInfo)
                let camera=scene.activeCamera
                camera.position=sceneInfo.cameraInfo.position
                camera.rotation=sceneInfo.cameraInfo.rotation        
            },500)
        } else {
            setPose(sceneInfo.poseInfo)
            let camera=scene.activeCamera
            camera.position=sceneInfo.cameraInfo.position
            camera.rotation=sceneInfo.cameraInfo.rotation        
        }
    }

