     */
    export async function renderNormal() {
        let oldMode=mode       
        setMode("")
        await wait()
        let url=canvas.toDataURL()             
        setMode(oldMode)
        return url
    }
    /**
     * current mode
     * @param  {("edit_skeleton" | "move_rotate"  | "scale" | "")} 
     */
    export let mode=""
    export function setMode(newMode) {
        if (newMode==="edit_skeleton" && rotationGizmo && positionGizmo) {
            if (rotationGizmo) rotationGizmo.attachedNode =null
            if (positionGizmo) positionGizmo.attachedNode =null
            if (scaleGizmo) scaleGizmo.attachedNode =null
            if (axisScaleGizmo) axisScaleGizmo.attachedNode=null
        }
        if (newMode==="move_rotate" && root) {
            rotationGizmo.attachedNode=root.getTransformNode()
            positionGizmo.attachedNode=root.getTransformNode()
            if (scaleGizmo) scaleGizmo.attachedNode =null
            if (axisScaleGizmo) axisScaleGizmo.attachedNode=null
        }
        if (newMode==="scale" && rotationGizmo && positionGizmo) {
            rotationGizmo.attachedNode =null
            positionGizmo.attachedNode =null           
            if (axisScaleGizmo) axisScaleGizmo.attachedNode=null
            if (scaleGizmo) scaleGizmo.attachedNode =root.getTransformNode()
        }        
        if (newMode==="" && rotationGizmo && positionGizmo) {
            rotationGizmo.attachedNode =null
            positionGizmo.attachedNode =null            
            scaleGizmo.attachedNode =null
            if (axisScaleGizmo) axisScaleGizmo.attachedNode=null
        }           
    }


    function dispatchChange(info) {
        host.dispatchEvent( new CustomEvent("change",info))
    }
    let rotationGizmo
    let positionGizmo
    let scaleGizmo
    let axisScaleGizmo
