    /**
     * add Gizmos to joints and main body for rotation
     * @param bodyMeshes
     */
    function addGizmo(bodyMeshes,skeleton) {
        //proxySphere = BABYLON.MeshBuilder.CreateSphere( "proxy" , { segments: 4, diameter: .06 },   scene)

         proxySphere = BABYLON.MeshBuilder.CreateBox("box", {height: 0.06, width: 0.06, depth: .06});

        proxySphere.renderingGroupId = 2
        proxySphere.isVisible=false


        root=skeleton.bones[0]
        // Create utility layer the gizmo will be rendered on
        let utilLayer = new BABYLON.UtilityLayerRenderer(scene);
        // position Gizmo
        positionGizmo=new BABYLON.PositionGizmo(utilLayer)
        // Create the gizmo and attach to the box
        rotationGizmo = new BABYLON.RotationGizmo(utilLayer)

        scaleGizmo = new BABYLON.ScaleGizmo(utilLayer)

        positionGizmo.xGizmo.dragBehavior.onDragEndObservable.add(() => {
            dispatchChange({type:"position",axis:"x"})
        })
        positionGizmo.yGizmo.dragBehavior.onDragEndObservable.add(() => {
            dispatchChange({type:"position",axis:"y"})
        })       
        positionGizmo.zGizmo.dragBehavior.onDragEndObservable.add(() => {        
            dispatchChange({type:"position",axis:"z"})
        })           
        
        
        /**
         * rotation
        */
        rotationGizmo.xGizmo.dragBehavior.onDragEndObservable.add(() => {
            rotationGizmoActive=false
            proxySphere.isVisible=false
            dispatchChange({type:"rotation",axis:"x"})
        })
        rotationGizmo.yGizmo.dragBehavior.onDragEndObservable.add(() => {
            rotationGizmoActive=false
            proxySphere.isVisible=false
            dispatchChange({type:"rotation",axis:"y"})
        })   

        rotationGizmo.zGizmo.dragBehavior.onDragEndObservable.add(() => {
            rotationGizmoActive=false
            proxySphere.isVisible=false
            dispatchChange({type:"rotation",axis:"z"})
        })   
        rotationGizmo.xGizmo.dragBehavior.onDragStartObservable.add(() => {
            currentAxis="x"
            currentRotationValue=proxySphere.rotation[currentAxis]
            rotationGizmoActive=true
        })
        rotationGizmo.yGizmo.dragBehavior.onDragStartObservable.add(() => {
            currentAxis="y"
            currentRotationValue=proxySphere.rotation[currentAxis]
            rotationGizmoActive=true
        })   

        rotationGizmo.zGizmo.dragBehavior.onDragStartObservable.add(() => {
            currentAxis="z"
            currentRotationValue=proxySphere.rotation[currentAxis]
            rotationGizmoActive=true
        })   




        scaleGizmo.xGizmo.dragBehavior.onDragEndObservable.add(() => {
            dispatchChange({type:"scale",axis:"x"})
        })
        scaleGizmo.yGizmo.dragBehavior.onDragEndObservable.add(() => {
            dispatchChange({type:"scale",axis:"y"})
        })   
        scaleGizmo.zGizmo.dragBehavior.onDragEndObservable.add(() => {
            dispatchChange({type:"scale",axis:"z"})
        })   
        let axisScaleGizmo = new BABYLON.AxisScaleGizmo(new BABYLON.Vector3(0,1,0), BABYLON.Color3.FromHexString("#00b894"), utilLayer);
        axisScaleGizmo.attachedNode=null
        // Keep the gizmo fixed to world rotation
        rotationGizmo.updateGizmoRotationToMatchAttachedMesh = true
        rotationGizmo.updateGizmoPositionToMatchAttachedMesh = true

        positionGizmo.updateGizmoPositionToMatchAttachedMesh = true        
        currentJointName=""
        scene.onPointerDown = function () {
            const pickInfo = scene.pick(this.pointerX, this.pointerY)
            if (pickInfo.hit && mode==="edit_skeleton") {
                if (pickInfo.pickedMesh.name.includes("joint_")) {
                    let jointMesh=pickInfo.pickedMesh
                    if (jointMesh.name===currentJointName) {
                        rotationGizmo.attachedNode =null
                        currentJointName=""
                    } else {
                        currentJointName=jointMesh.name
                        rotationGizmo.jointMesh=jointMesh
                        pickedTransformNode=jointMesh.bone.getTransformNode()
                        proxySphere.position=jointMesh.bone.getTransformNode().absolutePosition
             //           proxySphere.rotation=pickedTransformNode.rotation
                        proxySphere.isVisible=false
                        rotationGizmo.attachedNode =pickedTransformNode
                        axisScaleGizmo.attachedNode=null

//                        proxySphere.parent=jointMesh
//                        proxySphere.rotation=pickedTransformNode.rotation
                        var worldMatrix = pickedTransformNode.getWorldMatrix();
var quatRotation =  new BABYLON.Quaternion();
var position = new BABYLON.Vector3();
var scale = new BABYLON.Vector3();
worldMatrix.decompose(scale, quatRotation, position);                        
let euler=quatRotation.toEulerAngles()
proxySphere.rotation=euler

                    }                    
                }
                // change bone length
                if (pickInfo.pickedMesh.name.includes("bone_")) {

                 //   let bone = pickInfo.pickedMesh.bone
                 //   rotationGizmo.attachedNode=null
                 //   axisScaleGizmo.attachedNode=  bone.getTransformNode()
                }
            }
        }
    }
