    /**
     * show/hide all meshes from an array
     * @param meshes
     * @param enable
     */
    function showMeshes(meshes,enable) {
        if (!meshes) return
        for(let mesh of meshes) {
                if (enable) mesh.visibility=1.0
                else mesh.visibility=0.00001        // 0.0 does not work because it deactivates skeleton
        }
    }
    /**
     * default skeleton viewer - only for debugging
     * @param mesh
     * @param skeleton
     */
    export function addSkeletonViewer(mesh,skeleton) {
       let skeletonViewer = new BABYLON.Debug.SkeletonViewer(
                    skeleton,
                    mesh,
                    scene
                )
        skeletonViewer.isEnabled = true; // Enable it
        skeletonViewer.color = BABYLON.Color3.Red(); // Change default color from white to red*/
    }
    /**
     * set background to grey or transparent
     */
    export let background_type="transparent"

    let background=null
    /**
     * set background
     * @param ("grey"|"transparent"|"black")
    */
    export function setBackground(type="grey") {
        if (!scene) return
        if (type==="transparent") {
            if (background) {
                background.dispose()
                background=null
            }
            scene.clearColor = new BABYLON.Color4(0, 0, 0, 0)
        }
        if (type==="grey") {
            if (!background) {
                background  = scene.createDefaultEnvironment({ enableGroundShadow: true  })
                background.setMainColor(BABYLON.Color3.Gray())
                background.ground.position.y += 0.01    
            }
            scene.clearColor=clearColorDefault
        }
        if (type==="black") {
            if (background) {
                background.dispose()
                background=null
            }
            scene.clearColor= new BABYLON.Color4(0, 0, 0, 255)
        }
    }

    let clearColorDefault
    function initCanvas(canvas) {
        engine = new BABYLON.Engine(canvas, true, {
            preserveDrawingBuffer: true,
            stencil: true,
        })
        engine.enableOfflineSupport = false
        engine.resize()
        // This is really important to tell Babylon.js to use decomposeLerp and matrix interpolation
        BABYLON.Animation.AllowMatricesInterpolation = true

        scene = new BABYLON.Scene(engine)
        clearColorDefault=scene.clearColor
        // var camera = new BABYLON.ArcRotateCamera("camera1", Math.PI / 2, Math.PI / 4, 3, new BABYLON.Vector3(0, 1, 0), scene);
        let camera = new BABYLON.ArcRotateCamera("camera1",Math.PI / 2,Math.PI / 2, 3,new BABYLON.Vector3(0, 1, 0),scene)
        camera.attachControl(canvas, true)
        camera.lowerRadiusLimit = 1.2
        camera.upperRadiusLimit = 8
        camera.wheelDeltaPercentage = 0.01
        camera.onViewMatrixChangedObservable.add(() => {
                dispatchChange({type:"camera",position:camera.position,rotation:camera.rotation})
        })
        let light = new BABYLON.HemisphericLight("light1",new BABYLON.Vector3(0, 1, 0),scene)
        light.intensity = 0.6
        light.specular = BABYLON.Color3.Black()

        let light2 = new BABYLON.DirectionalLight( "dir01", new BABYLON.Vector3(0, -0.5, -1.0), scene)
        light2.position = new BABYLON.Vector3(0, 5, 5)

        // Shadows
        let shadowGenerator = new BABYLON.ShadowGenerator(1024, light2)
        shadowGenerator.useBlurExponentialShadowMap = true
        shadowGenerator.blurKernel = 32

        var groundMaterial = new BABYLON.GridMaterial("groundMaterial", scene);
        groundMaterial.majorUnitFrequency = 1;
        groundMaterial.minorUnitVisibility = 0.1;
        groundMaterial.gridRatio = 1;
        groundMaterial.backFaceCulling = false;
        groundMaterial.mainColor = new BABYLON.Color3(0, 0, 0);
        groundMaterial.lineColor = new BABYLON.Color3(0,0, 0);
        groundMaterial.opacity = 0.2;
        
        var ground = BABYLON.Mesh.CreateGround("ground1", 20, 20, 2, scene);
        ground.material = groundMaterial;

        BABYLON.SceneLoader.ImportMesh( "",path+"data/","dummy3.glb",scene,
            function (meshes, particleSystems, skeletons) {
                scene.stopAllAnimations();
                var skeleton = skeletons[0]
                var mesh = meshes[1] 
                loadedMeshes=meshes
                meshFigure=mesh            
                showMeshes(loadedMeshes,showHuman)   
                // Removing the ability of being picked for all meshes already in scene
                scene.meshes.forEach((element) => {
                    element.isPickable = false
                });

                shadowGenerator.addShadowCaster(scene.meshes[0], true);
                for (var index = 0; index < meshes.length; index++) {
                    meshes[index].receiveShadows = false;
                }


                setBackground(background_type)
                // add skeleton
                addSkeleton(mesh,skeleton)
                // add control to modify skeleton
                addGizmo([meshes[1],meshes[2]],skeleton) 
                engine.hideLoadingUI()
                startRenderLoop(engine, canvas)
             //   let p=getPose()
             //   setPose(p)
             //   addSkeletonViewer(mesh,skeleton) 
            }
        )

    }
    /**
     * canvas can be used somewhere else
     */
    export let canvas
</script>

<canvas id="renderCanvas" {width} {height} bind:this={canvas} class="canvas"/>

<style>
    .canvas {
        width:100%;
        height:100%;
    }
    * {
        box-sizing: border-box;
    }
